import * as actionTypes from '../actionTypes';


export const proxyInitialState = {
    node: {
        synced: false,
        start: 0,
        mining: false,
        height: 0,
        peers: 0,
        version: '',
        bestBlock: '',
        pk: '',
        status: 'RED',
        message: ""
    },
    apiKey: '',
    walletInitialized: false,
    walletLocked: true,
    wallet: {
        status: '',
        balance: 0,
        address: [],
    }
};

export const reducer = (state = proxyInitialState, action) => {
    switch (action.type) {
        case actionTypes.STORE_NODE_DATA:
            return {
                ...state,
                node: action.payload
            };
        case actionTypes.STORE_NODE_API_KEY:
            return {
                ...state,
                apiKey: action.payload.apiKey,
                walletInitialized: action.payload.walletInitialized,
                walletLocked: action.payload.walletLocked,
            };
        default:
    }
    return state
};

export default reducer;
