import axios from 'axios';
import { TOKEN_KEY } from "../build/constant";
// const WINDOWS_BASE_URL = (window._env_ === undefined ? null : window._env_.REACT_APP_BACKEND_URL);
// const DEFAULT_BASE_URL = WINDOWS_BASE_URL === undefined ? "https://api.ergopool.lan/api/" : WINDOWS_BASE_URL;
const DEFAULT_BASE_URL = "http://api.ergopool.lan/api/"
const BASE_URL = (window.backend === undefined ?  DEFAULT_BASE_URL : window.backend);
const instance = axios.create({baseURL: BASE_URL});

instance.interceptors.request.use(config => {
    const token = localStorage.getItem(TOKEN_KEY);
    if (token) {
        return {
            ...config,
            headers: {
                ...config.headers,
                Authorization: "Token " + token
            }
        }
    }
    return config;
});


instance.interceptors.response.use(response => response, error => {
    if (error.status === 401) {
        console.log("Im here with status unauthorized");
        localStorage.removeItem(TOKEN_KEY);
    }
    return Promise.reject(error);
});

