import React from 'react';
import MenuBar from '../main_layout/header/menu_bar/MenuBar';
import { NavLink } from "react-router-dom";
import Footer from '../main_layout/footer/Footer';
import NavigationToggle from "./navigation_toggle/NavigationToggle";
import { TOKEN_KEY } from "../../build/constant";

class AdminLayout extends React.Component {
    state = {
        navigationOpen: false,
    };

    componentWillMount() {
        console.log(localStorage.getItem(TOKEN_KEY));
        if (!localStorage.getItem(TOKEN_KEY)) {
            this.props.history.push("/admin/login/?next=" + this.props.match.url);
        }
    }

    toggleNavigation = () => {
        this.setState(prevState => {
            return {navigationOpen: !prevState.navigationOpen}
        });
    };

    logout = () => {
        localStorage.removeItem(TOKEN_KEY);
        this.props.history.push("/");
    };

    getUrl = (suffix) => "/admin/" + suffix;

    render = () => {
        let layoutClasses = ["user"];
        if (this.state.navigationOpen) {
            layoutClasses.push("active");
        }
        layoutClasses = layoutClasses.join(" ");
        return (
            <div>
                <div className={layoutClasses}>
                    <MenuBar/>
                    <ul className="navigation">
                        <NavigationToggle click={() => this.toggleNavigation()}/>
                        <li>
                            <NavLink activeClassName="active" to={this.getUrl("system/")}>
                                <i className="ep-icon-admin-dashboard"/>
                                <span>Dashboard</span>
                            </NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName="active" to={this.getUrl("user/")}>
                                <i className="ep-icon-admin-setting"/>
                                <span>Users</span>
                            </NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName="active" to={this.getUrl("manage/")}>
                                <i className="ep-icon-admin-management"/>
                                <span>Management</span>
                            </NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName="active" to={this.getUrl("ui-manage/")}>
                                <i className="ep-icon-admin-management-ui"/>
                                <span>UI management</span>
                            </NavLink>
                        </li>
                        <li>
                            <button onClick={this.logout}>
                                <i className="ep-icon-admin-logout"/>
                                <span>Logout</span>
                            </button>
                        </li>
                    </ul>
                    <div className="content">
                        {this.props.children}
                    </div>
                </div>
                <div className="clear"/>
                <div className="footer">
                    <Footer/>
                </div>
            </div>
        )
    };
}

export default AdminLayout;
