import React from 'react';
import DraggableTable from "../../../components/data_table/DraggableTable";

const items = [
    {key: "title", title: "title"},
    {key: "column", title: "column", choices:["Column 2", "Column 3", "Column 4"]},
    {key: "link", title: "link"},
    {key: "newTab", title: "Open link in new tab?", choices: ["no", "yes"]}
];

const extraItems = [
    {key: "column1", title: "Column 1 content"},
    {key: "column2", title: "Column 2 Title"},
    {key: "column3", title: "Column 3 Title"},
    {key: "column4", title: "Column 4 Title"},
];

const adminUiManageFooterLink = (props) => (
    <DraggableTable
        uiPath="FooterLink.json"
        items={items}
        extra={extraItems}
        objectKey="link"
        contentKey={null}
    />
);

export default adminUiManageFooterLink;
