import React from 'react';

class ChartFilter extends React.Component {
    state = {
        visible: false
    };

    changeState = () => {
        this.setState(prevState => {
            return {visible: !prevState.visible}
        })
    };

    apply = () => {
        this.props.onApply();
        this.changeState();
    };

    render() {
        return (
            <React.Fragment>
                <ul className={"chart-filter " + (this.state.visible ? "active" : "")}>
                    <div onClick={this.changeState}>
                        Custom Time period <i className="ep-icon-filter-chart"/>
                    </div>
                    <div className="chart-filter-content">
                        {this.props.children}
                        <div className="action-row">
                            <div className="btn-holder">
                                <button className="btn gray" onClick={this.changeState}>Cancel</button>
                            </div>
                            <div className="btn-holder">
                                <button className="btn blue" onClick={this.apply}>Apply</button>
                            </div>
                        </div>
                    </div>
                </ul>
                <ul className="chart-filter-series">
                    {
                        this.props.series.map(
                            value => (
                                <li key={value.key}>
                                    <span style={{backgroundColor: value.color}}/>
                                    {value.key}
                                </li>
                            )
                        )
                    }
                </ul>
            </React.Fragment>
        )
    };
}

export default ChartFilter;
