import React from 'react';

const miningParamForm = props => {
    const submit = event => {
        event.preventDefault();
        props.submit();
    };
    return (
        <form onSubmit={submit}>
            <div className="vertical-form">
                <div className="column">
                    <div className="content">
                        <label>Pool base factor</label>
                        <input
                            type="number"
                            className="input"
                            value={props.POOL_BASE_FACTOR}
                            onChange={event => props.save("POOL_BASE_FACTOR", event.target.value)}
                        />
                        <label>Latest allowed height for mining</label>
                        <input
                            type="number"
                            className="input"
                            value={props.THRESHOLD_HEIGHT}
                            onChange={event => props.save("THRESHOLD_HEIGHT", event.target.value)}
                        />
                        <label>Timestamp diff allowed to miner</label>
                        <input
                            type="number"
                            className="input"
                            value={props.THRESHOLD_TIMESTAMP}
                            onChange={event => props.save("THRESHOLD_TIMESTAMP", event.target.value)}
                        />
                        <div className="button-row">
                            <button className="btn blue" onClick={submit}>Save changes</button>
                        </div>
                    </div>
                </div>
                <div className="column">
                    <div className="content">
                        this is comments about this panel
                    </div>
                </div>
                <div className="clear"/>
            </div>
        </form>
    )
};


export default miningParamForm;
