import React from 'react';
import { Production } from "../../build/production";
import Address from "./address/Address";
import MenuBar from '../main_layout/header/menu_bar/MenuBar';
import MenuBarNavigation from '../main_layout/header/menu_bar/MenuBarNavigation';
import { NavLink } from "react-router-dom";
import Footer from '../main_layout/footer/Footer';
import NavigationHeader from "./navigation_header/NavigationHeader";
import NavigationToggle from "./navigation_toggle/NavigationToggle";
import { connect } from "react-redux";
import { setUserPk } from "../../store/actions/apiActions";

class UserLayout extends React.Component {
    state = {
        navigationOpen: true,
    };

    updateUserId = () => {
        if (Production.IsProxy()) {
            // const urlUserId=this.props.match.params.userId;
            // const stateUserId = this.props.userId;
        } else {
            const urlUserId = this.props.match.params.userId;
            const stateUserId = this.props.userId;
            if (urlUserId !== stateUserId) {
                this.props.setUserPk(urlUserId);
            }
        }
    };

    componentDidMount() {
        this.updateUserId();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.updateUserId();
    }

    toggleNavigation = () => {
        this.setState(prevState => {
            return {navigationOpen: !prevState.navigationOpen}
        });
    };

    getUrl = (suffix) => Production.IsServer() ? "/user/" + this.props.match.params.userId + suffix : suffix;

    render = () => {
        let layoutClasses = Production.IsServer() ? ["user"] : ["proxy", "user"];
        if (this.state.navigationOpen) {
            layoutClasses.push("active");
        }
        layoutClasses = layoutClasses.join(" ");
        return (
            <div>
                <div className={layoutClasses}>
                    <button onClick={() => this.toggleNavigation()} className="navigation-toggle-user">
                        <i className="ep-icon-hamburger-menu active-hide"/>
                    </button>
                    {Production.IsServer() ? <MenuBar/> : <NavigationHeader toggleNavigation={this.toggleNavigation}/>}
                    <ul className="navigation">
                        {Production.IsServer() ? <NavigationToggle click={() => this.toggleNavigation()}/> : null}
                        <li><NavLink activeClassName="active" to={this.getUrl("/")} exact>
                            <i className="ep-icon-user-dashboard"/><span>Dashboard</span>
                        </NavLink></li>
                        <li><NavLink activeClassName="active" to={this.getUrl("/wallet/")}>
                            <i className="ep-icon-user-wallet"/><span>Wallet</span>
                        </NavLink></li>
                        {/*<li><NavLink activeClassName="active" to={this.getUrl("/config/")}>*/}
                        {/*    <i className="ep-icon-user-setting"/><span>Settings</span>*/}
                        {/*</NavLink></li>*/}
                        {Production.IsProxy() ? (
                            <React.Fragment>
                                <hr/>
                                <li>
                                    <a href="/swagger" target="_blank">
                                        <i className="ep-icon-useralt-swagger"/>
                                        <span>Swagger</span>
                                    </a>
                                </li>
                                <li>
                                    <a href="http://explorer.ergoplatform.com" target="_blank">
                                        <i className="ep-icon-useralt-ergo-explorer"/>
                                        <span>Explorer</span>
                                    </a>
                                </li>
                                <li>
                                    <a href="http://ergoplatform.org" target="_blank">
                                        <i className="ep-icon-useralt-ergo1"/>
                                        <span>ErgoPlatform</span>
                                    </a>
                                </li>
                                <li>
                                    <a href="http://ergopool.io" target="_blank">
                                        <i className="ep-icon-logo-fill"/>
                                        <span>ErgoPool</span>
                                    </a>
                                </li>
                            </React.Fragment>
                        ) : (
                            <React.Fragment>
                                <li><NavLink activeClassName="active" to="/support/">
                                    <i className="ep-icon-user-help-desk"/><span>Support</span>
                                </NavLink></li>
                                <MenuBarNavigation/>
                            </React.Fragment>
                        )}
                    </ul>
                    <div className="content">
                        {Production.IsServer() ? (
                            <React.Fragment>
                                Welcome,
                                <Address userId={this.props.match.params.userId}/>
                            </React.Fragment>
                        ) : null}
                        {this.props.children}
                    </div>
                </div>
                <div className="clear"/>
                <Footer/>
            </div>
        )
    };
}

const mapStateToProps = state => ({
    dashboard: state.api.dashboard,
    userId: state.api.userId,
});

const mapDispatchToProps = dispatch => ({
    setUserPk: (userId) => dispatch(setUserPk(userId))
});


export default connect(mapStateToProps, mapDispatchToProps)(UserLayout);
