import React from 'react';
import Card from '../card_row/card/Card';
import CardTitle from '../card_row/card/CardTitle';
import CardContent from '../card_row/card/CardContent';
import CardCol from '../card_row/card_col/CardCol';
import Timer from '../../../../components/timer/Timer';
import moment from "moment";
import CustomCol from "./custom_col/CustomCol";
import CustomCard from './custom_col/custom_card/CustomCard';
import { connect } from "react-redux";

const proxyCardRow = (props) => {
    const node = props.node;
    const nodeStartDate = moment(new Date(node.start)).format('YYYY-MM-DD');
    const nodeStartTime = moment(new Date(node.start)).format(' HH:mm:ss');
    return (
        <React.Fragment>
            <CardCol>
                <Card>
                    <CardTitle>Node Started At</CardTitle>
                    <CardContent>
                        <Timer initial={node.start}/>
                        <CardContent className="sublime">{nodeStartDate}</CardContent>
                        <CardContent className="sublime">{nodeStartTime}</CardContent>
                    </CardContent>
                </Card>
            </CardCol>
            <CardCol>
                <Card customClass="card-half">
                    <CardTitle>Node State</CardTitle>
                    <CardContent
                        className="text-blue">{node.synced ? "synced" : "synchronization"}</CardContent>
                </Card>
                <Card customClass="card-half">
                    <CardTitle>Mining status</CardTitle>
                    <CardContent className="text-blue">{node.mining ? "Enabled" : "Disabled"}</CardContent>
                </Card>
            </CardCol>
            <CardCol>
                <Card customClass="card-half">
                    <CardTitle>Current Height</CardTitle>
                    <CardContent>{node.height}</CardContent>
                </Card>
                <Card customClass="card-half">
                    <CardTitle>Peers Connected</CardTitle>
                    <CardContent>{node.peers}</CardContent>
                </Card>
            </CardCol>
            <CustomCol>
                <CustomCard title="Node Version" value={node.version}/>
                <CustomCard title="Best Block id" value={node.bestBlock}/>
            </CustomCol>
        </React.Fragment>
    )
};


const mapStateToProps = state => {
    return {
        node: state.proxy.node
    };
};

export default connect(mapStateToProps)(proxyCardRow);
