import React from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from "react-redux";
import { setUserPk } from "../../../../store/actions/apiActions";


class Search extends React.Component {
    state = {
        user: '',
    };
    handleKeyDown = (e) => {
        if (e.key === 'Enter') {
            this.navigate();
        }
    };

    navigate = () => {
        this.props.setUserPk(this.state.user);
        this.props.history.push("/user/" + this.state.user + "/")
    };

    render = () => {
        return (
            <div className="search wrapper">
                <i
                    className="ep-icon-search"
                    onClick={this.navigate}
                />
                <input
                    type="text"
                    placeholder="Search your address"
                    value={this.state.user}
                    onKeyDown={this.handleKeyDown}
                    onChange={(event) => this.setState({user: event.target.value})}
                />
            </div>
        )
    };
}

const mapStateToProps = state => ({
    info: state.api.dashboard
});

const mapDispatchToProps = dispatch => ({
    setUserPk: () => dispatch(setUserPk)
});

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Search));
