import React from 'react';
import { connect } from "react-redux";
import withLayout from "../../../hoc/with_layout/withLayout";
import { setUserPk } from "../../../store/actions/apiActions";
import AdminLayout from "../AdminLayout";
import Address from '../../user_layout/address/Address';
import { NavLink } from "react-router-dom";

class AdminUserLayout extends React.Component {

    updateUserId = () => {
        const urlUserId = this.props.match.params.userId;
        const stateUserId = this.props.userId;
        if (urlUserId !== stateUserId) {
            this.props.setUserPk(urlUserId);
        }
    };

    componentDidMount() {
        this.updateUserId();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.updateUserId();
    }

    getUrl = prefix => "/admin/user/" + this.props.match.params.userId + prefix;
    render = () => {
        return (
            <React.Fragment>
                <Address userId={this.props.match.params.userId}>
                    <div className="admin-actions">
                        <NavLink to={this.getUrl('/')} exact={true}><i className="ep-icon-user-dashboard"/></NavLink>
                        <NavLink to={this.getUrl("/wallet/")} exact={true}><i
                            className="ep-icon-user-wallet"/></NavLink>
                        {/*<NavLink to="/" exact={true}><i className="ep-icon-user-dashboard"/></NavLink>*/}
                    </div>
                </Address>
                {this.props.children}
            </React.Fragment>
        )
    };
}

const mapStateToProps = state => ({
    dashboard: state.api.dashboard,
    userId: state.api.userId,
});

const mapDispatchToProps = dispatch => ({
    setUserPk: (userId) => dispatch(setUserPk(userId))
});

export default connect(mapStateToProps, mapDispatchToProps)(withLayout(AdminLayout)(AdminUserLayout));
