import React from 'react';
import * as formatter from '../../../formatter';

class HashRate extends React.Component {
    render() {
        return (
            <div className={"hashrate"}>
                <div>
                    <h2>Network Hashrate</h2>
                    <h4>(last 30 minutes)</h4>
                    <h2>{formatter.hashRate(this.props.networkHashRate)}H/s</h2>
                </div>
                <div>
                    <h2>Pool Hashrate</h2>
                    <h4>(last 30 minutes)</h4>
                    <h2>{formatter.hashRate(this.props.poolHashRate)}H/s</h2>
                </div>
            </div>
        )
    };
}

export default HashRate;
