import React from 'react';
import { connect } from "react-redux";
import { Production } from "../../../build/production";
import ZoomableChart from "../../../components/zoomable_chart/ZoomableChart";
import withLayout from "../../../hoc/with_layout/withLayout";
import UserLayout from "../../../layouts/user_layout/UserLayout";
import Panel from '../../../components/panel/Panel';
import moment from "moment";
import WalletBox from "./wallet_box/WalletBox";
// import Withdraw from './withdraw/Withdraw';
// import Payout from './payout/Payout';
import { ApiNetwork } from "../../../network/api";


class WalletPage extends React.Component {
    state = {
        data: [],
        income: {
            user: '',
            data: [],
        },
        payout: {
            user: '',
            data: [
                {date: new Date().getTime() / 1000, payout: 0},
            ]
        }
    };

    loadPayoutChart = (force = false) => {
        if (this.props.userId !== this.state.payout.user || force) {
            this.setState(state => {
                return {
                    ...state,
                    payout: {
                        ...state.payout,
                        user: this.props.userId
                    }
                }
            });
            ApiNetwork.payout(this.props.userId).then(response => {
                this.setState(state => {
                    let data = response.data.results;
                    console.log(data)
                    if (!data || data.length === 0)
                        data = [{date: new Date().getTime() / 1000, payout: 0}];
                    data.forEach(data_row => {
                        if(data_row.amount < 0){
                            data_row.amount = -data_row.amount;
                        }
                    })
                    return {
                        ...state,
                        payout: {
                            ...state.payout,
                            user: this.props.userId,
                            data: data,
                        }
                    }
                });
            });
        }
    };

    loadIncomeChart = (force = false) => {
        if (this.props.userId !== this.state.income.user || force) {
            this.setState(state => {
                return {
                    ...state,
                    income: {
                        ...state.income,
                        user: this.props.userId
                    }
                }
            });
            ApiNetwork.income(this.props.userId).then(response => {
                console.log(response.data)
                this.setState(state => {
                    return {
                        ...state,
                        income: {
                            ...state.income,
                            user: this.props.userId,
                            data: response.data,
                        }
                    }
                });
            });
        }
    };

    loadCharts = () => {
        this.loadPayoutChart();
        this.loadIncomeChart();
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.loadCharts();
    }

    componentDidMount() {
        this.loadCharts();
    };


    render() {
        return (
            <div>
                {Production.IsProxy() ? (
                    <Panel>
                        <WalletBox/>
                    </Panel>
                ) : null}
                {/*<Panel>*/}
                {/*    <Withdraw/>*/}
                {/*</Panel>*/}
                <Panel>
                    <ZoomableChart
                        height={337}
                        color="var(--azul)"
                        xKey="date"
                        yKey="amount"
                        xFormatter={timestamp => moment(new Date(1000 * timestamp)).format('YYYY:MM')}
                        // yFormatter={amount => amount/1e9}
                        data={this.state.payout.data}>

                    </ZoomableChart>
                </Panel>
                <Panel>
                    <ZoomableChart
                        height={337}
                        color="#42c3be"
                        xKey="height"
                        yKey="balance"
                        data={this.state.income.data}>

                    </ZoomableChart>
                </Panel>
                {/*<Payout/>*/}
            </div>
        )
    };
}


const mapStateToProps = state => ({
    info: state.api.dashboard,
    userId: state.api.userId,
});

const mapDispatchToProps = dispatch => ({});

export const WalletPageConnected = connect(mapStateToProps, mapDispatchToProps)(WalletPage);
export default withLayout(UserLayout)(WalletPageConnected);
