import React from 'react';
import withLayout from '../../../hoc/with_layout/withLayout';
import AdminLayout from '../../../layouts/admin_layout/AdminLayout'
import { ApiNetwork } from "../../../network/api";
import { hashRate, username } from '../../../formatter'
import DataTable from '../../../components/data_table/DataTable';

const columns = [
    {header: "User", key: "user", content: (row) => username(row.user)},
    {header: "Hash rate", key: "hash_rate", content: (row) => hashRate(row.hash_rate)},
    {header: "Valid Shares", key: "valid_shares"},
    {header: "Invalid Shares", key: "invalid_shares"},
    {header: "Last IP", key: "last_ip"},
    {header: "Status", key: "status", content: (row) => row.status},
];

class User extends React.Component {
    state = {
        "data": [],
        "sorting": "date",
        "sortingDir": "asc",
        "pageIndex": 0,
        "perPage": 10
    };

    loadUsers = () => {
        ApiNetwork.getUsers().then(response => {
            this.setState({data: response.results});
        });
    };

    componentWillMount() {
        this.loadUsers();
    }

    sort = columnIndex => {
        this.setState(state => {
            const dir = this.state.sorting === columnIndex && this.state.sortingDir === 'asc' ? "dest" : "asc";
            return {
                ...state,
                sorting: columnIndex,
                sortingDir: dir
            }
        });
    };

    rowSelect = userRow => {
        this.props.history.push("/admin/user/" + userRow.user + "/");
    };

    render() {
        return (
            <div className="panel custom-table">
                <DataTable
                    sort={this.sort}
                    sorting={this.state.sorting}
                    sortingDir={this.state.sortingDir}
                    data={this.state.data}
                    rowSelect={this.rowSelect}
                    columns={columns}/>
            </div>
        )
    };
}

export default withLayout(AdminLayout)(User);
