import React from 'react';

const withdrawForm = props => {
    const submit = event => {
        event.preventDefault();
        props.submit();
    };
    return (
        <form onSubmit={submit}>
            <div className="vertical-form">
                <div className="column">
                    <div className="content">
                        <label>Transaction fee of a transaction</label>
                        <input
                            type="number"
                            className="input"
                            value={props.TRANSACTION_FEE}
                            onChange={event => props.save("TRANSACTION_FEE", event.target.value)}
                        />
                        <label>Confirmation length for balances to be mature</label>
                        <input
                            type="number"
                            className="input"
                            value={props.CONFIRMATION_LENGTH}
                            onChange={event => props.save("CONFIRMATION_LENGTH", event.target.value)}
                        />
                        <label>Maximum number of outputs a transaction can have</label>
                        <input
                            type="number"
                            className="input"
                            value={props.MAX_NUMBER_OF_OUTPUTS}
                            onChange={event => props.save("MAX_NUMBER_OF_OUTPUTS", event.target.value)}
                        />
                        <div className="button-row">
                            <button className="btn blue" onClick={submit}>Save changes</button>
                        </div>
                    </div>
                </div>
                <div className="column">
                    <div className="content">
                        <label>Miner can not set his withdrawal threshold smaller than this</label>
                        <input
                            type="number"
                            className="input"
                            value={props.MIN_WITHDRAW_THRESHOLD}
                            onChange={event => props.save("MIN_WITHDRAW_THRESHOLD", event.target.value)}
                        />
                        <label>Miner can not set his withdrawal threshold bigger than this</label>
                        <input
                            type="number"
                            className="input"
                            value={props.MAX_WITHDRAW_THRESHOLD}
                            onChange={event => props.save("MAX_WITHDRAW_THRESHOLD", event.target.value)}
                        />
                        <label>Default value for periodic withdrawal if not set by miner explicitly</label>
                        <input
                            type="number"
                            className="input"
                            value={props.DEFAULT_WITHDRAW_THRESHOLD}
                            onChange={event => props.save("DEFAULT_WITHDRAW_THRESHOLD", event.target.value)}
                        />
                    </div>
                </div>
                <div className="clear"/>
            </div>
        </form>
    )
};


export default withdrawForm;
