import React from 'react';
import DraggableTable from '../../../components/data_table/DraggableTable';

const items = [
    {key: "title", title: "title"},
    {key: "color", title: "color"},
    {key: "superuser", title: "Only super user can see", choices:["no", "yes"]},
    {key: "link", title: "link"},
];

const initialLinks = ['/', '/support/', '/faq/', '/admin/'];

const adminUiManageMenuLink = (props) => (
    <DraggableTable
        uiPath="MenuLink.json"
        items={items}
        initial={initialLinks}
        objectKey="link"
        contentKey={null}
    />
);


export default adminUiManageMenuLink;
