import React from 'react';
import { Markdown } from 'react-showdown';
import withLayout from "../../../hoc/with_layout/withLayout";
import MainLayout from '../../../layouts/main_layout/MainLayout';
import { ApiNetwork } from "../../../network/api";
import { CompareObject } from "../../../utils";
import Error404 from '../../errors/error404/Error404';

class StaticPage extends React.Component {
    state = {
        pages: [],
        loaded: false
    };

    getData = () => {
        ApiNetwork.getUiJson("Page.json").then(pages=>{
            if(CompareObject(pages.items, this.state.pages) === false) {
                this.setState({pages: pages.items, loaded: true});
            }
        });
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.getData();
    }

    componentWillMount() {
        this.getData();
    }

    render = () => {
        let content = null;
        let title = null;
        console.log(this.state);
        this.state.pages.map(page => {
            if(this.props.location.pathname === page.urlKey){
                content = page.content;
                title = page.title;
            }
            return null;
        });
        if(content === null && this.state.loaded){
            return <Error404/>
        }
        const Component = withLayout(MainLayout, {title: title})((props) => {
            return <Markdown markup={content} />
        });
        return <div className="static-page"><Component/></div>
    };
}

export default StaticPage;
