import React from 'react';

const rewardForm = props => {
    const submit = event => {
        event.preventDefault();
        props.submit();
    };
    return (
        <form onSubmit={submit}>
            <div className="vertical-form">
                <div className="column">
                    <div className="content">
                        <label>Total reward of round</label>
                        <input
                            type="number"
                            className="input"
                            value={props.TOTAL_REWARD}
                            onChange={event => props.save("TOTAL_REWARD", event.target.value)}
                        />
                        <label>Total reward factor for pool transaction</label>
                        <input
                            type="number"
                            className="input"
                            value={props.REWARD_FACTOR}
                            onChange={event => props.save("REWARD_FACTOR", event.target.value)}
                        />
                        <label>Pool reward factor result precision</label>
                        <input
                            type="number"
                            className="input"
                            value={props.REWARD_FACTOR_PRECISION}
                            onChange={event => props.save("REWARD_FACTOR_PRECISION", event.target.value)}
                        />
                        <div className="button-row">
                            <button className="btn blue" onClick={submit}>Save changes</button>
                        </div>
                    </div>
                </div>
                <div className="column">
                    <div className="content">
                        <label>Pool fee factor</label>
                        <input
                            type="number"
                            className="input"
                            value={props.FEE_FACTOR}
                            onChange={event => props.save("FEE_FACTOR", event.target.value)}
                        />
                        <label>maximum reward anyone can receive in reward sharing</label>
                        <input
                            type="number"
                            className="input"
                            value={props.MAX_REWARD}
                            onChange={event => props.save("MAX_REWARD", event.target.value)}
                        />
                        <label>Reward algorithm used for sharing reward</label>
                        <select
                            className="input"
                            value={props.REWARD_ALGORITHM}
                            onChange={event => props.save("REWARD_ALGORITHM", event.target.value)}
                        >
                            <option value="prop">Prop</option>
                            <option value="pplns">PPLNS</option>
                        </select>
                        {props.REWARD_ALGORITHM === "pplns" ? (
                            <React.Fragment>
                                <label>Last share used in PPLNS to calculate reward of each user</label>
                                <input
                                    type="number"
                                    className="input"
                                    value={props.PPLNS_N}
                                    onChange={event => props.save("PPLNS_N", event.target.value)}
                                />
                            </React.Fragment>
                        ) : null}
                    </div>
                </div>
                <div className="clear"/>
            </div>
        </form>
    )
};


export default rewardForm;
