import React from 'react';
import ReCAPTCHA from "react-google-recaptcha";
import { ApiNetwork } from "../../../../network/api";

class SupportForm extends React.Component {
    state = {
        recaptcha: '',
        name: '',
        email: '',
        subject: '',
        message: '',
        siteKey: '',
        responseMessage: '',
        responseClass: '',
    };

    setKey = (key, value) => {
        this.setState({[key]: value})
    };

    getRecaptcha = () => {
        ApiNetwork.getSupport().then(data => {
            if(data.site_key !== this.state.siteKey){
                this.setState({siteKey: data.site_key});
            }
        })
    };

    componentWillMount() {
        this.getRecaptcha();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.getRecaptcha();
    }

    setResponseMessage = (message, className) => {
        this.setState({responseMessage: message, responseClass: className});
        setTimeout(()=>{this.setState({responseMessage: '', responseClass: ''})}, 5000);
    };

    submit = (event) => {
        event.preventDefault();
        ApiNetwork.support(
            this.state.name,
            this.state.email,
            this.state.subject,
            this.state.message,
            this.state.recaptcha
        ).then(response => {
            this.setResponseMessage("Successfully submitted", "success")
        }).catch(error=>{
            console.log(error);
            this.setResponseMessage("an error occurred", "error")
        })
    };

    render = () => {
        if(this.state.siteKey) {
            return (
                <form onSubmit={this.submit}>
                    <div className={this.state.responseClass}>{this.state.responseMessage}</div>
                    <label htmlFor="support_name">Full Name</label>
                    <input className="input"
                           id="support_name"
                           type="text"
                           value={this.state.name}
                           onChange={(event => this.setKey("name", event.target.value))}/>

                    <label htmlFor="support_email">Email Address</label>
                    <input className="input"
                           id="support_email"
                           type="text"
                           value={this.state.email}
                           onChange={(event => this.setKey("email", event.target.value))}/>

                    <label htmlFor="support_subject">Subject</label>
                    <input className="input"
                           id="support_subject"
                           type="text"
                           value={this.state.subject}
                           onChange={(event => this.setKey("subject", event.target.value))}/>
                    <label>
                        <ReCAPTCHA
                            sitekey={this.state.siteKey}
                            onChange={value => this.setKey('recaptcha', value)}
                        />
                    </label>
                    <label htmlFor="support_message">Message</label>
                    <textarea id="support_message"
                              className="textarea"
                              rows="5"
                              value={this.state.message}
                              onChange={(event => this.setKey("message", event.target.value))}>
                    </textarea>
                    <input type="submit" className="btn blue" value="Submit"/>
                </form>
            )
        }
        return null;
    };
}

export default SupportForm;
