import React from 'react';
import { connect } from "react-redux";
import { loadMenu } from "../../../../store/actions/apiActions";
import Menu from './menu/Menu';
import { TOKEN_KEY } from "../../../../build/constant";

class MenuBar extends React.Component {
    state = {
        toggled: false
    };

    componentWillMount() {
        this.props.loadMenu();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.props.loadMenu();
    }

    toggleClick = () => {
        this.setState(state => {
            return {...state, toggled: !state.toggled};
        })
    };

    render = () => {
        const className = "navigation-menu " + (this.state.toggled ? "toggled" : "");
        return (
            <li className={className}>
                <button className="title" onClick={this.toggleClick}>
                    Quick Access
                </button>
                <ul className="menu-bar-navigation" style={this.props.style}>
                    {this.props.links.map((link, index) => {
                        if (link.superuser === "yes" && !localStorage.getItem(TOKEN_KEY)) return null;
                        if (link.color) return <Menu key={link.link} link={link.link} title={link.title}
                                                     style={{color: link.color}}/>;
                        return <Menu key={link.link} link={link.link} title={link.title}/>;
                    })}
                </ul>
            </li>
        )
    };
}

const mapStateToProps = state => ({
    links: state.api.menu,
});

const mapDispatchToProps = dispatch => ({
    loadMenu: () => dispatch(loadMenu())
});

export default connect(mapStateToProps, mapDispatchToProps)(MenuBar);
