import { CompareObject } from "../../utils";
import * as actionTypes from '../actionTypes';
import { ApiNetwork } from '../../network/api';
import { store } from '../index'

export const loadApiInfoAsync = () => {
    return dispatch => {
        const getData = () => {
            ApiNetwork.info().then(response => {
                setTimeout(getData, 10 * 60 * 1000);
                dispatch({
                    type: actionTypes.STORE_API_INFO,
                    payload: {
                        ...response.data
                    }
                });
            }).catch(error => {
                setTimeout(getData, 10 * 60 * 1000);
            });
        };
        getData();
    }
};

export const loadApiDashboardAsync = (loadOnce = false) => {
    return dispatch => {
        const loadData = () => {
            const userId = store.getState().api.userId;
            if (userId) {
                ApiNetwork.dashboard(userId).then(response => {
                    if (!loadOnce) {
                        setTimeout(() => loadData(), 10 * 60 * 1000);
                    }
                    const payload = {
                        timestamp: new Date(response.data.timestamp.split(" ").join("T")),
                        hash_rate: response.data.hash_rate,
                        round_valid_shares: response.data.round_valid_shares,
                        round_invalid_shares: response.data.round_invalid_shares,
                        user: {...response.data.users[userId]}
                    };
                    dispatch({
                        type: actionTypes.STORE_API_USER_DASHBOARD,
                        payload: {...payload}
                    });
                }).catch(error => {
                    if (!loadOnce) {
                        setTimeout(() => loadData(), 10 * 60 * 1000);
                    }
                })
            }
        };
        loadData();
    }
};

export const setUserPk = (userId) => {
    return dispatch => {
        dispatch({
            type: actionTypes.SET_USER_PK,
            payload: userId
        });
        loadApiDashboardAsync(true);
    }
};

export const loadMenu = () => {
    return dispatch => {
        ApiNetwork.getUiJson('MenuLink.json').then(data => {
            if (CompareObject(store.getState().api.menu, data.items) === false) {
                dispatch({
                    type: actionTypes.STORE_MENU_LINKS,
                    payload: data.items
                });
            }
        });
    }
};

export const loadFooter = () => {
    return dispatch => {
        ApiNetwork.getUiJson('FooterLink.json').then(data => {
            if (CompareObject(store.getState().api.footer, data) === false) {
                dispatch({
                    type: actionTypes.STORE_FOOTER,
                    payload: data
                });
            }
        });
    }
};

export const storeApiKey = (apiKey) => ({
    type: actionTypes.SET_USER_API_KEY,
    payload: apiKey
});

export const storeMessage = messages => {
    const state = store.getState();
    const newMessages = [...state.api.messages];
    messages.map(message => {
        let msgIndex = -1;
        newMessages.map((msg, index) => {
            if (CompareObject(msg, message, ['display'])) {
                msgIndex = index;
                if (msg.display === "yes") {
                    message.display = "yes";
                }
            }
            return null;
        });
        if (msgIndex === -1) {
            newMessages.push({...message});
        } else {
            newMessages[msgIndex] = {...message};
        }
        return null;
    });
    return {
        type: actionTypes.STORE_MESSAGES,
        payload: newMessages
    };
};

export const loadMessagesAsync = () => {
    return dispatch => {
        const getData = () => {
            ApiNetwork.getUiJson("Notification.json").then(response => {
                setTimeout(getData, 10 * 60 * 1000);
                try {
                    dispatch(storeMessage(response.items));
                } catch (e) {

                }
            }).catch(error => {
                setTimeout(getData, 10 * 60 * 1000);
            });
        };
        getData();
    }
};
