import React from 'react';
import { connect } from "react-redux";
import Modal from "../../../components/modal/Modal";
import proxyNetworking from "../../../network/proxy";
import NavigationToggle from "../navigation_toggle/NavigationToggle";
import { storeApiKey } from "../../../store/actions/proxyActions";


class NavigationHeader extends React.Component {
    state = {
        authModal: false,
        unlockModal: false,
        initialModal: false,
        apiKey: '',
        unlockPassword: ''
    };

    openAuthPanel = () => {
        this.setState({authModal: true});
    };

    closeAuthPanel = () => {
        this.setState({authModal: false, apiKey: ''});
    };

    openUnlockPanel = () => {
        this.setState({unlockModal: true});
    };

    closeUnlockPanel = () => {
        this.setState({unlockModal: false});
    };

    setApiKey = () => {
        proxyNetworking.walletStatus(this.state.apiKey, response => {
            console.log(response);
            this.props.storeApiKey(this.state.apiKey, response.data.isInitialized, !response.data.isUnlocked);
            this.setState({authModal: false, apiKey: ''});
        }, error => {
            alert("invalid Api key");
        });
    };

    lockWallet = () => {
        proxyNetworking.walletLock(response => {
            this.props.storeApiKey(this.props.apiKey, this.props.walletInitialized, true);
        }, error => {

        })
    };

    setUnlockPassword = () => {
        proxyNetworking.walletUnlock(this.state.unlockPassword, response => {
            this.props.storeApiKey(this.props.apiKey, this.props.walletInitialized, false);
            this.setState({unlockModal: false, unlockPassword: ''});
        }, error => {

        });
    };

    reloadProxyConfig = () => {
        proxyNetworking.proxyReload(response => {
        });
    };

    render() {
        let statusClassName = "pr-";
        if (this.props.node.status === "GREEN") statusClassName += "success";
        if (this.props.node.status === "RED") statusClassName += "error";
        if (this.props.node.status === "YELLOW") statusClassName += "warning";
        return (
            <div>
                <Modal
                    display={this.state.authModal}
                    onClose={this.closeAuthPanel}
                    onSuccess={this.setApiKey}
                    successTitle="Set Api key"
                >
                    <h3>Set Api key</h3>
                    <p>Authorization</p>
                    <p>Set api key to access node requests</p>
                    <input value={this.state.apiKey}
                           onChange={(event) => this.setState({apiKey: event.target.value})}/>
                </Modal>
                <Modal
                    display={this.state.unlockModal}
                    onClose={this.closeUnlockPanel}
                    onSuccess={this.setUnlockPassword}
                    successTitle="Unlock Wallet">
                    <p>Enter wallet password</p>
                    <input value={this.state.unlockPassword}
                           type="password"
                           onChange={(event) => this.setState({unlockPassword: event.target.value})}/>
                </Modal>
                <div className={"panel-header " + statusClassName}>
                    <ul className="navigation">
                        <NavigationToggle click={this.props.toggleNavigation}/>
                    </ul>
                    <div className="content-wrapper">
                        <div className="logo">
                            <div className="head-title">ErgoPool</div>
                            <div className="head-sub-title">Proxy Interface</div>
                        </div>
                        <div className="action-button">
                            <div className="floater"/>
                            {this.props.apiKey ? (
                                <React.Fragment>
                                    <button className="btn blue" onClick={this.openAuthPanel}>Update Api key</button>
                                    {!this.props.walletInitialized ? (
                                        <button className="btn bordered" onClick={this.openAuthPanel}>Initialize
                                            Wallet</button>
                                    ) : this.props.walletLocked ?
                                        <button className="btn bordered" onClick={this.openUnlockPanel}>UnLock
                                            Wallet</button> :
                                        <button className="btn bordered" onClick={this.lockWallet}>lock Wallet</button>
                                    }
                                </React.Fragment>
                            ) : <button className="btn blue set-api-key" onClick={this.openAuthPanel}>Set Api key</button>
                            }
                            <button className="btn bordered icon-button" onClick={this.reloadProxyConfig}
                                    title="Reload pool configuration"><i className="ep-icon-refresh"/></button>
                        </div>
                        <div className="proxy-status">
                            {this.props.node.message ? (
                                <React.Fragment>
                                    <i className="ep-icon-information"/>
                                    <div className="pr-info">
                                        {this.props.node.message}
                                    </div>
                                </React.Fragment>) : null}
                        </div>
                    </div>
                </div>
            </div>
        )
    };
}

const mapStateToProps = state => {
    return {
        apiKey: state.proxy.apiKey,
        walletInitialized: state.proxy.walletInitialized,
        walletLocked: state.proxy.walletLocked,
        node: state.proxy.node
    }
};

const mapDispatchToProps = dispatch => {
    return {
        storeApiKey: (apiKey, walletInitialized, walletLocked) => dispatch(storeApiKey(apiKey, walletInitialized, walletLocked)),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(NavigationHeader);
