import React from 'react';


const connector = (props) => {
    const rotate = Math.atan((props.to - props.from));
    const classes = (props.from === 0 && props.to === 0) ? "connector red" : "connector";
    const styles = {
        transform: "rotate(" + (rotate / Math.asin(1) * 90) + "deg)",
    };
    if (props.from !== props.to){
        styles['width'] = (1/Math.sin(rotate) * 100 * (props.to - props.from)) + "%";
    }
    return (
        <div className={classes} style={styles}>

        </div>
    )
};

export default connector;
