import React from 'react';
import { Production } from "../build/production";
import AdminDashboardInfo from "../page/admin/dashboard/AdminDashboard";
import Error404 from "../page/errors/error404/Error404";
import Error500 from "../page/errors/error500/Error500";
import ErrorMaintenance from "../page/errors/error_maintenance/errorMaintenance";
import ErrorComing from "../page/errors/error_coming/errorComing";
import Index from "../page/main/Index";
import StaticPage from "../page/main/static/StaticPage";

import UserMainPage from '../page/user/main/MainPage';
import UserConfigPage from '../page/user/config/ConfigPage';
import UserWalletPage from "../page/user/wallet/WalletPage";

import Faq from "../page/main/faq/Faq";
import Support from "../page/main/support/Support";
import { loadNodeDataAsync } from "../store/actions/proxyActions";
import { loadApiDashboardAsync, loadApiInfoAsync, loadMessagesAsync } from "../store/actions/apiActions";
import { BrowserRouter, Route, Redirect, Switch } from 'react-router-dom';
import { connect } from "react-redux";

import AdminUser from '../page/admin/user/User';
import AdminManage from '../page/admin/manage/Manage';
import AdminUserMain from '../page/admin/user/UserMain/UserMainPage';
import AdminUserWallet from '../page/admin/user/UserWallet/UserWalletPage';
import AdminUiManageFaq from '../page/admin/ui_manage/AdminUiManageFaq';
import AdminUiManageStatic from '../page/admin/ui_manage/AdminUiManageStatic';
import AdminUiManageMenuLink from '../page/admin/ui_manage/AdminUiManageMenuLink';
import AdminUiManageFooterLink from '../page/admin/ui_manage/AdminUiManageFooterLink';
import AdminUiManageIndexContent from '../page/admin/ui_manage/AdminUiManageIndexContent';
import AdminUiManageNotification from '../page/admin/ui_manage/AdminUiManageNotification';
import AdminLogin from '../page/admin/login/Login';

class App extends React.Component {
    componentDidMount() {
        this.props.getApiInfo();
        this.props.getApiDashboard();
        if (Production.IsProxy()) {
            this.props.getNodeData();
        } else {
            this.props.getMessages();
        }
    }

    render = () => {
        return (
            <BrowserRouter basename={Production.IsProxy() ? "dashboard" : ""}>
                <div className="App">
                    {Production.IsServer() ? (
                        <Switch>
                            <Route path="/error/404/" component={Error404}/>
                            <Route path="/error/500/" component={Error500}/>
                            <Route path="/error/maintenance/" component={ErrorMaintenance}/>
                            <Route path="/error/coming/" component={ErrorComing}/>

                            <Route path="/support/" component={Support}/>
                            <Route path="/faq/" exact component={Faq}/>
                            <Route path="/user/:userId/" exact component={UserMainPage}/>
                            <Route path="/user/:userId/wallet/" exact component={UserWalletPage}/>
                            <Route path="/user/:userId/config/" exact component={UserConfigPage}/>

                            <Route path="/" exact component={Index}/>

                            <Route path="/admin/login/" exact component={AdminLogin}/>
                            <Route path="/admin/system/" exact component={AdminDashboardInfo}/>
                            <Route path="/admin/system/stat/" exact component={AdminDashboardInfo}/>
                            <Route path="/admin/system/block/" exact component={AdminDashboardInfo}/>

                            <Route path="/admin/user/" exact component={AdminUser}/>
                            <Route path="/admin/user/:userId/" exact component={AdminUserMain}/>
                            <Route path="/admin/user/:userId/wallet/" exact component={AdminUserWallet}/>
                            <Route path="/admin/manage/" exact component={AdminManage}/>
                            <Route path="/admin/ui-manage/faq/" exact component={AdminUiManageFaq}/>
                            <Route path="/admin/ui-manage/static/" exact component={AdminUiManageStatic}/>
                            <Route path="/admin/ui-manage/menu/" exact component={AdminUiManageMenuLink}/>
                            <Route path="/admin/ui-manage/footer/" exact component={AdminUiManageFooterLink}/>
                            <Route path="/admin/ui-manage/index/" exact component={AdminUiManageIndexContent}/>
                            <Route path="/admin/ui-manage/notification/" exact component={AdminUiManageNotification}/>
                            <Redirect path="/admin/ui-manage/" to="/admin/ui-manage/faq"/>
                            <Redirect path="/admin/" to="/admin/system/"/>
                            <Route path="/" component={StaticPage}/>

                            <Redirect to="/error/404"/>
                        </Switch>
                    ) : (
                        <Switch>
                            <Route path="/error/404/" component={Error404}/>
                            <Route path="/" exact component={UserMainPage}/>
                            <Route path="/wallet/" exact component={UserWalletPage}/>
                            {/*<Route path="/config/" exact component={UserConfigPage}/>*/}
                            <Redirect to="/error/404"/>
                        </Switch>
                    )}
                </div>
                {/*<div className={}*/}
            </BrowserRouter>
        );
    };
}


const mapStateToProps = () => ({});


const mapDispatchToProps = dispatch => {
    const res = {
        getApiInfo: () => dispatch(loadApiInfoAsync()),
        getMessages: () => dispatch(loadMessagesAsync()),
        getApiDashboard: () => dispatch(loadApiDashboardAsync(false))
    };
    if (Production.IsProxy()) {
        res['getNodeData'] = () => dispatch(loadNodeDataAsync())
    }
    return res
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
