import React from 'react';
import { NavLink } from "react-router-dom";
import AdminLayout from '../AdminLayout';
import withLayout from '../../../hoc/with_layout/withLayout';

class AdminSystemLayout extends React.Component {

    getUrl = (suffix) => "/admin/system/" + suffix;

    render = () => {
        return (
            <React.Fragment>
                <ul className="tab-bar-flex">
                    <li>
                        <NavLink to={this.getUrl("")} exact>System Info</NavLink>
                    </li>
                    <li>
                        <NavLink to={this.getUrl("stat")} exact>Statistics</NavLink>
                    </li>
                    <li>
                        <NavLink to={this.getUrl("block")} exact>Blocks</NavLink>
                    </li>
                </ul>
                {this.props.children}
            </React.Fragment>
        )
    };
}

export default withLayout(AdminLayout)(AdminSystemLayout);
