import React from 'react';
import withLayout from '../../../hoc/with_layout/withLayout';
import Panel from '../../../components/panel/Panel';
import AdminLayout from '../../../layouts/admin_layout/AdminLayout'
import { ApiNetwork } from "../../../network/api";
import MiningParamForm from "./MiningParamForm";
import RewardForm from "./RewardForm";
import WithdrawForm from "./WithdrawForm";

const REWARD_KEYS = [
    "TOTAL_REWARD",
    "REWARD_FACTOR",
    "REWARD_FACTOR_PRECISION",
    "FEE_FACTOR",
    "MAX_REWARD",
    "PPLNS_N",
    "REWARD_ALGORITHM",
];

const WITHDRAW_KEYS = [
    "TRANSACTION_FEE",
    "MAX_NUMBER_OF_OUTPUTS",
    "MIN_WITHDRAW_THRESHOLD",
    "MAX_WITHDRAW_THRESHOLD",
    "DEFAULT_WITHDRAW_THRESHOLD",
    "CONFIRMATION_LENGTH",

];

const MINING_KEYS = [
    "POOL_BASE_FACTOR",
    "THRESHOLD_HEIGHT",
    "THRESHOLD_TIMESTAMP",
];

class Manage extends React.Component {
    state = {
        reward: {
            TOTAL_REWARD: null,
            REWARD_FACTOR: null,
            REWARD_FACTOR_PRECISION: null,
            FEE_FACTOR: null,
            MAX_REWARD: null,
            PPLNS_N: null,
            REWARD_ALGORITHM: "PPLNS",
        },
        withdraw: {
            TRANSACTION_FEE: null,
            MAX_NUMBER_OF_OUTPUTS: null,
            MIN_WITHDRAW_THRESHOLD: null,
            MAX_WITHDRAW_THRESHOLD: null,
            DEFAULT_WITHDRAW_THRESHOLD: null,
            CONFIRMATION_LENGTH: null,
        },
        mining: {
            POOL_BASE_FACTOR: null,
            THRESHOLD_HEIGHT: null,
            THRESHOLD_TIMESTAMP: null,
        }
    };

    savePanelValue = (panel, key, value) => {
        this.setState(state => {
            return {
                ...state,
                [panel]: {
                    ...state[panel],
                    [key]: value
                }
            }
        })
    };

    submitPanel = panel => {
        ApiNetwork.setConfig(this.state[panel]);
    };

    componentWillMount() {
        ApiNetwork.getConfig().then(response => {
            const reward = Object.keys(response)
                .filter(key => REWARD_KEYS.includes(key))
                .reduce((obj, key) => {
                    obj[key] = response[key];
                    return obj;
                }, {});
            const withdraw = Object.keys(response)
                .filter(key => WITHDRAW_KEYS.includes(key))
                .reduce((obj, key) => {
                    obj[key] = response[key];
                    return obj;
                }, {});
            const mining = Object.keys(response)
                .filter(key => MINING_KEYS.includes(key))
                .reduce((obj, key) => {
                    obj[key] = response[key];
                    return obj;
                }, {});
            this.setState({
                reward: reward,
                withdraw: withdraw,
                mining: mining
            });
        })
    }

    render() {
        return (
            <React.Fragment>
                <div>Mining parameters</div>
                <Panel>
                    <MiningParamForm
                        save={(key, value) => this.savePanelValue("mining", key, value)}
                        submit={() => this.submitPanel("mining")}
                        {...this.state.mining} />
                </Panel>
                <div>Reward</div>
                <Panel>
                    <RewardForm
                        save={(key, value) => this.savePanelValue("reward", key, value)}
                        submit={() => this.submitPanel("reward")}
                        {...this.state.reward} />
                </Panel>
                <div>withdraw</div>
                <Panel>
                    <WithdrawForm
                        save={(key, value) => this.savePanelValue("withdraw", key, value)}
                        submit={() => this.submitPanel("withdraw")}
                        {...this.state.withdraw} />
                </Panel>
            </React.Fragment>
        )
    };
}

export default withLayout(AdminLayout)(Manage);
