import React from 'react';

class Earning extends React.Component {
    state = {
        hashrate: ""
    };

    blockInDay = () => {
        let hashrate = 0
        if(this.state.hashrate)
            hashrate = this.state.hashrate * 1024 * 1024;
        const networkHashRate = this.props.network + hashrate;
        if (networkHashRate === 0) return 0;
        return hashrate / networkHashRate * 24 * 30;
    };

    earning = () => {
        const res = this.blockInDay() * 65;
        if (res > 0)
            return res.toFixed(4);
        return 0;
    };

    collateral = () => {
        const res = (Math.ceil(this.blockInDay()) + 2) * 65 + 0.001;
        return res
    };


    render() {
        return (
            <div className="earning">
                <label>
                    Autolykos Hashrate(MH/s)
                    <input value={this.state.hashrate}
                           onChange={(event) => this.setState({hashrate: event.target.value})}/>
                </label>
                <span> * 24H = {this.earning()} Erg</span>
                <h2>Required collateral: {this.collateral()} Erg</h2>
                <h1>Daily earning: {this.earning()} Erg</h1>
            </div>
        )
    };
}

export default Earning;
