import * as actionTypes from '../actionTypes';


export const apiInitialState = {
    userId: '',
    token: '',
    messages: [],
    menu: [],
    footer: {
        extra: {},
        items: []
    },
    info: {
        hash_rate: {
            network: 24843969,
            pool: 1
        },
        miners: 0,
        active_miners: 0,
        price: {
            btc: null,
            usd: null
        },
        blocks_in_hour: 0.0
    },
    dashboard: {
        round_valid_shares: 4454,
        round_invalid_shares: 0,
        timestamp: new Date(),
        hash_rate: 70068,
        user: {
            round_valid_shares: 0,
            round_invalid_shares: 0,
            immature: 0,
            mature: 0,
            withdraw: 0,
            hash_rate: 1
        }
    }
};

export const reducer = (state = apiInitialState, action) => {
    switch (action.type) {
        case actionTypes.STORE_API_INFO:
            return {
                ...state,
                info: action.payload
            };
        case actionTypes.STORE_API_USER_DASHBOARD:
            return {
                ...state,
                dashboard: action.payload
            };
        case actionTypes.SET_USER_PK:
            return {
                ...state,
                userId: action.payload
            };
        case actionTypes.SET_USER_API_KEY:
            return {
                ...state,
                token: action.payload
            };
        case actionTypes.STORE_MENU_LINKS:
            return {
                ...state,
                menu: action.payload
            };
        case actionTypes.STORE_FOOTER:
            return {
                ...state,
                footer: action.payload
            };
        case actionTypes.STORE_MESSAGES:
            return {
                ...state,
                messages: action.payload
            };
        default:
    }
    return state
};

export default reducer;
