import React from 'react';

class Timer extends React.Component {
    state = {
        initial: new Date().getTime(),
        seconds:0,
        minutes:0,
        hours:0
    };
    padDigit = value => value < 10 ? "0" + value : value;
    componentDidMount() {
        this.start();
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        this.start();
    }
    start = () => {
        setTimeout(()=>{
            const initial = (this.props.initial ?  this.props.initial : this.state.initial);
            const currentTime = new Date().getTime();
            const diff = parseInt((currentTime - initial) / 1000);
            const seconds = diff % 60;
            const minutes = parseInt(diff / 60) % 60;
            const hours = parseInt(diff / 3600);
            this.setState({
                seconds: seconds,
                minutes: minutes,
                hours: hours
            })
        }, 1000)

    };

    render() {
        return (
            <div>
                <div className="timer-hour">{this.state.hours}</div>
                <div className="timer-minute"> :{this.padDigit(this.state.minutes)}</div>
                <div className="timer-second"> :{this.padDigit(this.state.seconds)}</div>
            </div>
        )
    };
}

export default Timer;
