import React from 'react';
import { connect } from "react-redux";
import { loadMenu } from "../../../../store/actions/apiActions";
import Menu from './menu/Menu';
import { TOKEN_KEY } from "../../../../build/constant";

class MenuBar extends React.Component {
    state={
        toggled: false
    };

    componentWillMount() {
        this.props.loadMenu();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.props.loadMenu();
    }
    toggleClick = () => {
        this.setState(state=>{
            return {...state, toggled: !state.toggled};
        })
    };

    render = () => {
        const className = "main-menu-bar " + (this.state.toggled ? "toggled" : "");
        return (
            <div className={className}>
                <div className="wrapper">
                    <div className="logo"/>
                    <button className="navigation-toggle" onClick={this.toggleClick}>
                        <i className="ep-icon-hamburger-menu"/>
                        <i className="ep-icon-close"/>
                    </button>
                    <ul className="menu-bar" style={this.props.style}>
                        {this.props.links.map((link, index) => {
                            if (link.superuser === "yes" && !localStorage.getItem(TOKEN_KEY)) return null;
                            if (link.color) return <Menu key={link.link} link={link.link} title={link.title} style={{color: link.color}}/>;
                            return <Menu key={link.link} link={link.link} title={link.title}/>;
                        })}
                    </ul>
                </div>
            </div>
        )
    };
}

const mapStateToProps = state => ({
    links: state.api.menu,
});

const mapDispatchToProps = dispatch => ({
    loadMenu: () => dispatch(loadMenu())
});

export default connect(mapStateToProps, mapDispatchToProps)(MenuBar);
