import React from 'react';
import { ApiNetwork } from "../../../network/api";
import Accordion from './Accordion/Accordion';
import withLayout from '../../../hoc/with_layout/withLayout';
import MainLayout from '../../../layouts/main_layout/MainLayout';


class Faq extends React.Component {
    state = {
        questions: []
    };

    componentWillMount() {
        ApiNetwork.getUiJson("Faq.json").then(data => {
            this.setState({questions: data.items});
        })
    }

    render() {
        console.log(this.state.questions);
        return (
            <div className="faq">
                <Accordion rows={this.state.questions}/>
                <div className="support">
                    Can't find what you are looking for? Ask us in Support section
                </div>
            </div>
        )
    };
}

export default withLayout(MainLayout, {
    title: 'Frequently Asked Questions'
})(Faq);
