import React from 'react';
import apiNetworking from '../../../../network/api';

class WalletBox extends React.Component {
    state = {
        amount: 0,
        address: '',
        walletAmount: 0
    };
    onChange = (key, value) => {
        this.setState({[key]: value});
    };
    onSubmit = () => {
        apiNetworking.post('/balance/', {
            "miner": this.props.userId,
            "balance": this.state.amount
        }).then(response => {

        }, error => {

        });
    };

    render() {
        return (
            <div className="split-panel">
                <div className="split-panel-form">
                    <label>
                        Recipient address
                        <input value={this.state.address}
                               onChange={event => this.onChange("address", event.target.value)}/>
                    </label>
                    <label>
                        Amount
                        <input value={this.state.amount}
                               onChange={event => this.onChange("amount", event.target.value)}/>
                    </label>
                    <button className="btn blue" onClick={this.onSubmit}> Submit </button>
                </div>
                <div className="split-panel-text">
                    <h1>Wallet Balance</h1>
                    <h1>{this.state.walletAmount}</h1>
                    <h1>Erg</h1>
                </div>
            </div>
        )
    };
}

export default WalletBox;
