import React from 'react';
import withLayout from '../../hoc/with_layout/withLayout';
import AdminUiManageLayout from '../../layouts/admin_layout/admin_ui_manage_layout/AdminUiManageLayout';
import { ApiNetwork } from "../../network/api";
import ReactMde from "react-mde";
import "react-mde/lib/styles/css/react-mde-all.css";
import * as Showdown from "showdown";
// import { Reorder } from "../../utils";

const converter = new Showdown.Converter({
    tables: true,
    simplifiedAutoLink: true,
    strikethrough: true,
    tasklists: true
});

class FaqUiManage extends React.Component {

    state = {
        extra: {},
        elements: [],
        edit: -1,
    };

    initialElements = () => {
        if (this.props.initial && this.props.objectKey) {
            const elements = [...this.state.elements];
            const available = elements.map(item => item[this.props.objectKey]);
            let addedElement = 0;
            this.props.initial.map(item => {
                if (available.indexOf(item) < 0) {
                    const row = this.createEmptyRow();
                    row[this.props.objectKey] = item;
                    elements.push(row);
                    addedElement += 1;
                }
                return null;
            });
            if (addedElement > 0) {
                this.setState({elements: elements});
            }
        }
    };

    createEmptyRow = () => {
        const res = {};
        this.props.items.map(element => {
            res[element.key] = "";
            if (element.choices) {
                res[element.key] = element.choices[0];
            }
            return null;
        });
        if (this.props.content) {
            res[this.props.content] = '';
        }
        return res;
    };

    addRow = () => {
        this.setState(state => {
            const newElement = this.createEmptyRow();
            return {
                ...state,
                elements: [
                    ...state.elements,
                    newElement
                ],
                edit: state.elements.length,
                editElement: newElement
            }
        })
    };

    removeRow = (index) => {
        this.setState(state => {
            return {
                ...state,
                elements: state.elements.filter((elem, elemIndex) => elemIndex !== index)
            }
        })
    };

    editRow = index => {
        const selectedElement = this.state.elements[index];
        this.setState({
            edit: index,
            editElement: {...selectedElement}
        });
    };

    saveChanges = () => {
        this.setState(state => ({
            edit: -1,
            elements: state.elements.map((question, index) => {
                return index === state.edit ? state.editElement : question
            }),
            editElement: {}
        }));
    };

    submit = () => {
        const postData = {items: [...this.state.elements]};
        if(this.props.extra)
            postData.extra = {...this.state.extra};
        ApiNetwork.setUiJson(this.props.uiPath, postData).then(response => {
            console.log(response);
        }).catch(error => {
            console.log(error);
        })
    };

    componentWillMount() {
        ApiNetwork.getUiJson(this.props.uiPath).then(data => {
            if(this.props.extra && data.extra){
                this.setState({elements: data.items, extra: data.extra, edit: -1});
            }else{
                this.setState({elements: data.items, edit: -1});
            }
        });
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initialElements();
    }

    storeValue = (key, value) => {
        this.setState(state => {
            return {
                ...state,
                editElement: {
                    ...state.editElement,
                    [key]: value
                }
            };
        })
    };

    storeExtraValue = (key, value) => {
        this.setState(state => {
            return {
                ...state,
                extra: {
                    ...state.extra,
                    [key]: value
                }
            };
        })
    };

    render() {
        return (
            <React.Fragment>
                {this.props.extra ? (
                    <div className="panel">
                        <div className="panel-content">
                            {this.props.extra.map(item => (
                                <label>
                                    {item.title}
                                    <input
                                        type="text"
                                        value={this.state.extra[item.key]}
                                        onChange={(event => this.storeExtraValue(item.key, event.target.value))}/>
                                </label>
                            ))}
                        </div>
                    </div>
                ) : null}
                <div className="panel custom-table">
                    <table className="left-align">
                        <thead>
                        <tr>
                            {this.props.items.map(item => <th key={item.key}>{item.title}</th>)}
                            <th width={200}>Action</th>
                        </tr>
                        </thead>
                        <tbody>
                        {this.state.elements.map((dataRow, index) => (
                            <React.Fragment key={index}>
                                <tr className={index === this.state.edit ? "edit-row" : null}>
                                    {this.props.items.map(item => {
                                        return (<td key={item.key}>
                                            {index === this.state.edit ? (item.choices ? (
                                                <select
                                                    className="input"
                                                    onChange={event => this.storeValue(item.key, event.target.value)}
                                                    value={this.state.editElement[item.key]}>
                                                    {item.choices.map(item => <option key={item}
                                                                                      value={item}>{item}</option>)}
                                                </select>
                                            ) : (
                                                <input type="text" className="input"
                                                       value={this.state.editElement[item.key]}
                                                       onChange={event => this.storeValue(item.key, event.target.value)}/>
                                            )) : (
                                                <React.Fragment>
                                                    {/*<i className="ep-icon-move"/>*/}
                                                    &nbsp;{dataRow[item.key]}
                                                </React.Fragment>
                                            )}
                                        </td>)
                                    })}
                                    <td className="center">
                                        {index === this.state.edit ? (
                                            <React.Fragment>
                                                <button onClick={this.saveChanges}>
                                                    <i className="ep-icon-edit"/>
                                                </button>
                                                <button onClick={() => this.setState({edit: -1})}>
                                                    <i className="ep-icon-close"/>
                                                </button>
                                            </React.Fragment>
                                        ) : (
                                            <React.Fragment>
                                                <button onClick={() => this.editRow(index)}>
                                                    <i className="ep-icon-edit"/>
                                                </button>
                                                {this.props.objectKey && this.props.initial && this.props.initial.find(value => value === dataRow[this.props.objectKey]) ? null : (
                                                    <button onClick={() => this.removeRow(index)}>
                                                        <i className="ep-icon-delete"/>
                                                    </button>
                                                )}
                                            </React.Fragment>
                                        )}
                                    </td>
                                </tr>
                                {(index === this.state.edit && this.props.contentKey) ? (
                                    <tr className="edit-row">
                                        <td colSpan={this.props.items.length + 1}>
                                            <ReactMde
                                                value={this.state.editElement[this.props.contentKey]}
                                                selectedTab={this.state.editorSelectedTab}
                                                onTabChange={value => this.setState({editorSelectedTab: value})}
                                                generateMarkdownPreview={markdown =>
                                                    Promise.resolve(converter.makeHtml(markdown))
                                                }
                                                onChange={value => this.setState(state => {
                                                    return {
                                                        ...state,
                                                        editElement: {
                                                            ...state.editElement,
                                                            [this.props.contentKey]: value
                                                        }
                                                    }
                                                })}/>
                                        </td>
                                    </tr>
                                ) : null}
                            </React.Fragment>
                        ))}
                        </tbody>
                        <tfoot>
                        <tr>
                            <td colSpan={this.props.items.length + 1} style={{padding: 10}}>
                                <button onClick={this.addRow}>
                                    <i className="ep-icon-plus"/> Add new
                                </button>
                                {this.state.edit === -1 ? (
                                    <button className="btn blue" onClick={this.submit} style={{float: "right"}}>
                                        Save
                                    </button>
                                ) : null}
                            </td>
                        </tr>
                        </tfoot>
                    </table>
                </div>
            </React.Fragment>
        )
    };
}

export default withLayout(AdminUiManageLayout)(FaqUiManage);
