import React from 'react';


const dataTable = (props) => {
    return (
        <table>
            <thead>
            <tr>
                {props.columns.map(column => (
                    <th key={column.key} className="sorted">
                        <button className="sort-button"
                                onClick={() => props.sort ? props.sort(column.key) : null}>
                            {column.header} &nbsp;
                            {column.key === props.sorting ? props.sortingDir === 'asc' ?
                                <i className="ep-icon-sorted"/> : <i className="ep-icon-sorted flipped"/> :
                                <i className="ep-icon-sort"/>}
                        </button>
                    </th>
                ))}
            </tr>
            </thead>
            <tbody>
            {props.data.map(dataRow => (
                <tr key={dataRow.key}>
                    {props.columns.map(column => (
                        <td key={column.key} onClick={() => props.rowSelect ? props.rowSelect(dataRow) : null}>
                            {column.content ? column.content(dataRow) : <span>{dataRow[column.key]}</span>}
                        </td>
                    ))}
                </tr>
            ))}
            {props.footer ? (
                <tr colSpan={props.columns.length}>
                    <td>
                        {this.props.footer}
                    </td>
                </tr>
            ) : null}
            </tbody>
        </table>
    )
};

export default dataTable;
