const suffix = ['', 'K', 'M', 'G', 'T', 'E', 'P'];

export const hashRate = value => {
    if (value === undefined)
        return '0 ';
    let index = 0;
    while (value > 1024) {
        index += 1;
        value /= 1024;
    }
    return value.toFixed(2) + ' ' + suffix[index];
};


export const ergo = value => {
    const price = value / 1e9;
    const pieces = parseFloat(price).toFixed(6).split('');
    let ii = pieces.length - 10;
    while ((ii-=3) > 0) {
        pieces.splice(ii, 0, ',')
    }
    return pieces.join('')
};

export const username = value => {
    return (value.length < 12) ? value : value.substr(0, 12) + '...';
};
