import React from 'react';
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { loadFooter } from "../../../store/actions/apiActions";


class Footer extends React.Component {
    state = {
        links: [],
        extra: {}
    };

    componentWillMount() {
        this.props.loadFooter();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.props.loadFooter();
    }

    renderLink = (link, index) => {
        if (link.link.startsWith("http://") || link.link.startsWith("https://") || link.newTab === "yes") {
            if (link.newTab === "yes") {
                return <a target="_blank" rel="noopener noreferrer" href={link.link} className="link" key={index}>{link.title}</a>
            } else {
                return <a href={link.link} className="link" key={index}>{link.title}</a>
            }
        } else {
            return <Link to={link.link} className="link" key={index}>{link.title}</Link>
        }
    };

    render = () => {
        const columns = ["column2", "column3", "column4"];
        return (
            <div className="footer" style={{backgroundColor: this.props.color}}>
                <div className="wrapper">
                    <div className="row">
                        <div className="col">
                            <div className="big-text">{this.props.footer.extra.column1}</div>
                        </div>
                        {columns.map(column => (
                            <div className="col" key={column}>
                                <div className="title">{this.props.footer.extra[column]}</div>
                                {this.props.footer.items.map((link, index) => {
                                    return link.column.replace(" ", "").toLowerCase() === column ? this.renderLink(link, index) : null;
                                })}
                            </div>
                        ))}
                    </div>
                    <div className="split"/>
                    <div className="row">
                        <div className="col">
                            <div className="logo">Ergopool</div>
                        </div>
                        <div className="col">
                            <div className="social-links">
                                <a className="social-link" href="https://t.me/joinchat/AAAAAFkhFbEskHGp0p-JLg"><i
                                    className="ep-icon-social-telegram"/></a>
                                <a className="social-link" href="https://twitter.com/ergopool"><i
                                    className="ep-icon-social-twitter"/></a>
                                <a className="social-link" href="https://discord.gg/CzqeQjE"><i
                                    className="ep-icon-social-discord"/></a>
                                <a className="social-link" href="https://github.com/ergopool-io"><i
                                    className="ep-icon-social-github"/></a>
                            </div>
                        </div>
                        <div className="col">
                            <div className="copyright">
                                2019-2020 Ergopool
                            </div>
                        </div>
                        <div className="col">
                        </div>
                        <div className="clear"/>
                    </div>
                </div>
            </div>
        )
    };
}

const mapStateToProps = state => ({
    footer: state.api.footer,
});

const mapDispatchToProps = dispatch => ({
    loadFooter: () => dispatch(loadFooter())
});

export default connect(mapStateToProps, mapDispatchToProps)(Footer);
