import React from 'react';
import { Markdown } from "react-showdown";
import { storeMessage } from "../../store/actions/apiActions";
import { connect } from "react-redux";


class Popup extends React.Component {

    getCurrentMessages = () => {
        let resultMessages = [];
        this.props.messages.map((message, index) => {
            if (message.display !== "yes") {
                resultMessages.push(message);
            }
            return null
        });
        return resultMessages;
    };

    closeAllMessages = () => {
        const newMessages = this.props.messages.map(message => {
            return {...message, display: "yes"};
        });
        this.props.storeMessage(newMessages);
    };

    nextMessage = (message) => {
        this.props.storeMessage([{...message, display: "yes"}])
    };

    render = () => {
        const messages = this.getCurrentMessages();
        if (messages.length === 0)
            return null;
        const message = messages[0];
        const className = "popup popup-" + message.type;
        return (
            <div className="popup-container">
                <div className={className}>
                    <div className="action-row">
                        <button className="close-btn" onClick={this.closeAllMessages}>
                            <i className="ep-icon-close"/>
                        </button>
                    </div>
                    <div className="popup-content">
                        <h1>{message.title}</h1>
                        <Markdown markup={message.content}/>
                    </div>
                    <div className="action-row-bottom">
                        <button className="action-btn" onClick={() => this.nextMessage(message)}>
                            {messages.length > 1 ? "Next" : "Close"}
                        </button>
                    </div>
                </div>
            </div>
        )
    };
}

const mapStateToProps = state => ({
    messages: state.api.messages,
});

const mapDispatchToProps = dispatch => ({
    storeMessage: (message) => dispatch(storeMessage(message))
});

export default connect(mapStateToProps, mapDispatchToProps)(Popup);
