import React from 'react';
import MenuBar from "../../../layouts/main_layout/header/menu_bar/MenuBar";
import { ApiNetwork } from "../../../network/api";
import ReCAPTCHA from "react-google-recaptcha";
import { TOKEN_KEY } from "../../../build/constant";
import queryString from 'query-string';

class Login extends React.Component {
    state = {
        username: '',
        password: '',
        recaptcha: '',
        siteKey: ''
    };

    saveContent = (key, value) => {
        this.setState({[key]: value});
    };

    redirect = () => {
        const queryParams = queryString.parse(this.props.location.search);
        if (queryParams.next) {
            this.props.history.push(queryParams.next);
        } else {
            this.props.history.push("/admin/")
        }
    };

    componentWillMount() {
        if (localStorage.getItem(TOKEN_KEY)) {
            this.redirect();
        } else {
            ApiNetwork.getLogin().then(data => {
                this.setState({siteKey: data.site_key})
            })
        }
    }

    signIn = (event) => {
        event.preventDefault();
        ApiNetwork.login(this.state.username, this.state.password, this.state.recaptcha).then(response => {
            localStorage.setItem(TOKEN_KEY, response.token);
            this.redirect();
        })
    };

    render() {
        return (
            <div className="login">
                <div className="header">
                    <MenuBar/>
                    <div className="clear"/>
                </div>
                <div className="content">
                    <form className="with40" onSubmit={this.signIn}>
                        <h1>Sign in</h1>
                        {this.state.siteKey ? (
                            <React.Fragment>
                                <label>Username</label>
                                <input
                                    type="text"
                                    // className="input"
                                    value={this.state.username}
                                    onChange={event => this.saveContent("username", event.target.value)}
                                />
                                <label>Password</label>
                                <input
                                    type="password"
                                    // className="input"
                                    value={this.state.password}
                                    onChange={event => this.saveContent("password", event.target.value)}
                                />
                                <div className="button-row">
                                    <ReCAPTCHA
                                        sitekey={this.state.siteKey}
                                        onChange={value => this.saveContent('recaptcha', value)}
                                    />
                                </div>
                                <div className="button-row">
                                    <button className="btn blue" onClick={this.signIn}>Sign In</button>
                                </div>
                            </React.Fragment>
                        ) : null}
                    </form>
                </div>
            </div>
        )
    };
}

export default Login;
