import React from 'react';
import ProxyCardRow from "../proxy_card_row/ProxyCardRow";
import Card from './card/Card';
import CardTitle from './card/CardTitle';
import CardTitle2 from './card/CardTitle2';
import CardFilter from './card/CardFilter';
import CardContent from './card/CardContent';
import CardCol from './card_col/CardCol';
import Timer from '../../../../components/timer/Timer';
import * as formatter from '../../../../formatter';
import { connect } from "react-redux";
import { Production } from '../../../../build/production';

const PERIOD_STATES = ["Daily", "Weekly", "Monthly"];

class CardRow extends React.Component {
    state = {
        period: "Daily"
    };

    rotatePeriod = () => {
        this.setState(state => {
            const index = (PERIOD_STATES.indexOf(state.period) + 1) % PERIOD_STATES.length;
            return {
                ...state,
                period: PERIOD_STATES[index]
            }
        });
    };

    render = () => {
        let contribution = 1;
        if (this.props.round_valid_shares)
            contribution = (this.props.dashboard.round_valid_shares / this.props.round_valid_shares);
        contribution = (100 * contribution).toFixed(1);
        const totalShare = this.props.dashboard.round_valid_shares + this.props.dashboard.round_invalid_shares;
        const validShareRatio = (totalShare > 0 ? 100 * this.props.dashboard.round_valid_shares / totalShare : 100).toFixed(1);
        const invalidShareRatio = (totalShare > 0 ? 100 * this.props.dashboard.round_invalid_shares / totalShare : 0).toFixed(1);
        const hashRate = this.props.info.hash_rate;
        let earning = 0;
        if (hashRate.network !== 1) {
            earning = 720.0 * (this.props.dashboard.user.hash_rate.avg - 1) / (hashRate.network - 1) * 67500000000;
        }
        if (this.state.period === "Weekly") {
            earning *= 7;
        } else if (this.state.period === "Monthly") {
            earning *= 30;
        }
        return (
            <div className="card-row">
                <CardCol>
                    <Card>
                        <CardTitle>Time Elapsed</CardTitle>
                        <CardContent>
                            <Timer initial={this.props.dashboard.timestamp}/>
                        </CardContent>
                        <CardTitle>Contribution</CardTitle>
                        <CardContent>{contribution}%</CardContent>
                    </Card>
                </CardCol>
                <CardCol>
                    <Card customClass="card-half">
                        <CardTitle>Immature balance</CardTitle>
                        <CardContent>{formatter.ergo(this.props.dashboard.user.immature)} Erg</CardContent>
                    </Card>
                    <Card customClass="card-half">
                        <CardTitle>Mature balance</CardTitle>
                        <CardContent>{formatter.ergo(this.props.dashboard.user.mature)} Erg</CardContent>
                    </Card>
                </CardCol>
                <CardCol>
                    <Card>
                        <CardTitle>Shares (Round)</CardTitle>
                        <CardTitle2>Valid {validShareRatio}%</CardTitle2>
                        <CardContent>{this.props.dashboard.user.round_valid_shares}</CardContent>
                        <CardTitle2>Invalid {invalidShareRatio}%</CardTitle2>
                        <CardContent>{this.props.dashboard.user.round_invalid_shares}</CardContent>
                    </Card>
                </CardCol>
                <CardCol>
                    <Card>
                        <CardTitle>Hashrate</CardTitle>
                        <CardTitle2>Current <br/>(30 minutes)</CardTitle2>
                        <CardContent>{formatter.hashRate(this.props.dashboard.user.hash_rate.current)}H/s</CardContent>
                        <CardTitle2>Average (24h)</CardTitle2>
                        <CardContent>{formatter.hashRate(this.props.dashboard.user.hash_rate.avg)}H/s</CardContent>
                    </Card>
                </CardCol>
                <CardCol>
                    <Card>
                        <CardTitle>Estimated Earning</CardTitle>
                        <CardContent>{formatter.ergo(earning)} Erg</CardContent>
                        <CardFilter>Time period filter</CardFilter>
                        <CardContent>
                            {this.state.period}
                            <i className="ep-icon-filter" onClick={this.rotatePeriod}/>
                        </CardContent>
                    </Card>
                </CardCol>
                {Production.IsProxy() ? (
                    <ProxyCardRow/>
                ) : null}
                <div className="clear"/>
            </div>
        )
    };
}

const mapStateToProps = state => ({
    dashboard: state.api.dashboard,
    api: state.api,
    info: state.api.info,
});

export default connect(mapStateToProps)(CardRow);
