import React from "react";
import {
    BarChart,
    Bar,
    LineChart,
    Line,
    CartesianGrid,
    XAxis,
    YAxis,
    Tooltip,
    ReferenceArea, ResponsiveContainer
} from "recharts";

export default class StreamingDemo extends React.Component {
    state = {
        refAreaLeft: "",
        refAreaRight: "",
        zoomStart: "",
        zoomEnd: "",
        sliceData: []
    };

    zoom() {
        let {refAreaLeft, refAreaRight} = this.state;
        if (refAreaLeft === refAreaRight || refAreaRight === "") {
            this.setState(() => ({
                refAreaLeft: "",
                refAreaRight: ""
            }));
            return;
        }
        // xAxis domain
        if (refAreaLeft > refAreaRight)
            [refAreaLeft, refAreaRight] = [refAreaRight, refAreaLeft];
        const leftIndex = this.props.data.findIndex(item => item[this.props.xKey] === refAreaLeft);
        const rightIndex = this.props.data.findIndex(item => item[this.props.xKey] === refAreaRight);
        this.setState((oldState) => ({
            refAreaLeft: "",
            refAreaRight: "",
            zoomStart: refAreaLeft,
            zoomEnd: refAreaRight,
            sliceData: this.props.data.slice(leftIndex, rightIndex),
        }));
    }
    onMouseDown(e) {
        try {
            this.setState({refAreaLeft: e.activeLabel});
        }catch (e) {

        }
    }
    onMouseMove(e) {
        try {
            if(this.state.refAreaLeft)
                this.setState({refAreaRight: e.activeLabel})
        }catch (e) {

        }
    }

    xFormatter = value => {
        console.log(value);
        if(this.props.xFormatter){
            return this.props.xFormatter(value)
        }
        return value;
    };

    render() {
        let sliceData = this.state.sliceData;
        if(sliceData.length === 0)
            sliceData = this.props.data;
        return (
            <div className="highlight-bar-charts">
                <ResponsiveContainer height={337}>
                    <BarChart data={sliceData}>
                        <XAxis
                            tickFormatter={this.xFormatter}
                            dataKey={this.props.xKey}/>
                        <YAxis/>
                        <CartesianGrid strokeDasharray="3 3"/>
                        <Tooltip isAnimationActive={false}/>
                        <Bar
                            isAnimationActive={false}
                            dataKey={this.props.yKey}
                            stroke={this.props.color}
                            fill={this.props.color}/>
                    </BarChart>
                </ResponsiveContainer>
                <ResponsiveContainer height={100}>
                    <LineChart data={this.props.data}
                               onMouseDown={this.onMouseDown.bind(this)}
                               onMouseMove={this.onMouseMove.bind(this)}
                               onMouseUp={this.zoom.bind(this)}>
                        <XAxis dataKey={this.props.xKey}/>
                        <YAxis/>
                        <CartesianGrid strokeDasharray="3 3"/>
                        <Line
                            type="monotone"
                            dataKey={this.props.yKey}
                            stroke={this.props.color}
                            fill={this.props.color}
                            isAnimationActive={false}/>
                        {this.state.refAreaLeft && this.state.refAreaRight ? (
                        <ReferenceArea
                            x1={this.state.refAreaLeft}
                            x2={this.state.refAreaRight}
                            strokeOpacity={0.3}
                        />) : (<ReferenceArea
                            x1={0}
                            x2={this.state.zoomStart}
                            strokeOpacity={0.3}
                        />)}
                        {this.state.refAreaLeft && this.state.refAreaRight ? null :<ReferenceArea
                                x1={this.state.zoomEnd}
                                x2={this.props.data.length - 1}
                                strokeOpacity={0.3}
                            />}
                    </LineChart>
                </ResponsiveContainer>
            </div>
        )
    }
}
