import * as actionTypes from '../actionTypes';
import proxyNetwork from '../../network/proxy';

export const loadNodeDataAsync = () => {
    return dispatch => {
        const getData = () => {
            proxyNetwork.info(
                response => {
                    const data = response.data;
                    setTimeout(getData, 5000);
                    dispatch({
                        type: actionTypes.STORE_NODE_DATA,
                        payload: {
                            synced: data.fullHeight === data.headersHeight,
                            start: data.launchTime,
                            mining: data.isMining,
                            height: data.fullHeight,
                            peers: data.peersCount,
                            version: data.appVersion,
                            bestBlock: data.previousFullHeaderId,
                            pk: data.proxy.miner.pk,
                            status: data.proxy.status.health,
                            message: data.proxy.status.reason
                        }
                    });
                }, error => {
                    setTimeout(getData, 10000);
                }
            );
        };
        getData();
    }
};


export const storeApiKey = (apiKey, walletInitialized, walletLocked) => ({
    type: actionTypes.STORE_NODE_API_KEY, payload: {
        apiKey: apiKey,
        walletInitialized: walletInitialized,
        walletLocked: walletLocked
    }
});
