import React from 'react';
import { NavLink } from "react-router-dom";
import AdminLayout from '../AdminLayout';
import withLayout from '../../../hoc/with_layout/withLayout';

class AdminUiManageLayout extends React.Component {

    getUrl = (suffix) => "/admin/ui-manage/" + suffix;

    render = () => {
        return (
            <React.Fragment>
                <ul className="tab-bar-flex">
                    <li>
                        <NavLink to={this.getUrl("faq")} exact>Faq content</NavLink>
                    </li>
                    <li>
                        <NavLink to={this.getUrl("static")} exact>Static pages</NavLink>
                    </li>
                    <li>
                        <NavLink to={this.getUrl("menu")} exact>Menu links</NavLink>
                    </li>
                    <li>
                        <NavLink to={this.getUrl("footer")} exact>Footer links</NavLink>
                    </li>
                    <li>
                        <NavLink to={this.getUrl("index")} exact>Index</NavLink>
                    </li>
                    <li>
                        <NavLink to={this.getUrl("notification")} exact>Notifications</NavLink>
                    </li>
                </ul>
                {this.props.children}
            </React.Fragment>
        )
    };
}

export default withLayout(AdminLayout)(AdminUiManageLayout);
