import React from 'react';
import Panel from '../../../../components/panel/Panel';
import Connector from './Connector';

const rowsNodes = [1, 2, 1, 1, 5, 2, 3, 1, 1, 1];
const connectors = [
    [[1]],
    [],
    [],
    [[1, 2, 3, 4]],
    [[], [1]],
    [[1], [2]],
    [],
    [],
    [],
    []
];

const MiningTree = (props) => {
    // const elements = Array(10).fill().map((_, i) => i);
    return (
        <Panel>
            <div className="circle-view">
                {rowsNodes.map((cnt, index) => (
                    <div className="row">
                        {Array(cnt).fill().map((_, col_index) => (
                            <div className="holder">
                                <div className="container">
                                    <div className="circle">
                                        13 MH/s {col_index}
                                    </div>
                                </div>
                                {(index === 9 || col_index > 0) ? null : <Connector from={0} to={0}/>}
                                {
                                    connectors[index].length > col_index ?
                                        connectors[index][col_index].map(connect_to => (
                                            <Connector from={col_index} to={connect_to}/>)
                                        ) : null
                                }
                            </div>
                        ))}
                    </div>
                ))}
            </div>
        </Panel>
    )
};

export default MiningTree;
