import React from 'react';
import { connect } from "react-redux";
import withLayout from "../../../hoc/with_layout/withLayout";
import UserLayout from '../../../layouts/user_layout/UserLayout';
import ChartFilter from "../chart_filter/ChartFilter";
import CardRow from './card_row/CardRow';
import Panel from '../../../components/panel/Panel';
import DatePicker from "react-datepicker";
import TimePicker from 'rc-time-picker';
import moment from 'moment';
import "react-datepicker/dist/react-datepicker.css";
import 'rc-time-picker/assets/index.css';
import { ApiNetwork } from "../../../network/api";
import * as formatter from '../../../formatter';
import { LineChart, Legend, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import { loadApiDashboardAsync } from "../../../store/actions/apiActions";

const hashRateSeries = [
    {key: "current", color: "#42c3be"},
    {key: "avg", color: "#5d62b5"},
];

const shareSeries = [
    {key: "valid", color: "#379f2c"},
    {key: "invalid", color: "#d85e5a"},
];

class MainPage extends React.Component {
    state = {
        hashRate: {
            user: '',
            data: [],
            fromDate: null,
            fromTime: null,
            toDate: null,
            toTime: null,
        },
        share: {
            user: '',
            data: [],
            fromDate: null,
            fromTime: null,
            toDate: null,
            toTime: null,
        },
        userId: ''
    };

    setHashRateState = (key, value) => {
        this.setState(state => {
            return {
                ...state,
                hashRate: {
                    ...state.hashRate,
                    [key]: value
                }
            }
        })
    };

    setShareState = (key, value) => {
        this.setState(state => {
            return {
                ...state,
                share: {
                    ...state.share,
                    [key]: value
                }
            }
        })
    };

    getTimeStamp = (fromDate, fromTime) => {
        if (fromDate && fromTime) {
            const dateStr = fromDate.toDateString();
            const timeStr = new Date(fromTime.toString()).toTimeString();
            const startDateTime = new Date(dateStr + " " + timeStr);
            return startDateTime.getTime() / 1000;
        }
        return null;
    };

    loadHashRateChart = (force = false) => {
        if (this.props.userId !== this.state.hashRate.user || force) {
            this.setState(state => {
                return {
                    ...state,
                    hashRate: {
                        ...state.hashRate,
                        user: this.props.userId
                    }
                }
            });
            let fromTimeStamp = this.getTimeStamp(this.state.hashRate.fromDate, this.state.hashRate.fromTime);
            let toTimeStamp = this.getTimeStamp(this.state.hashRate.toDate, this.state.hashRate.toTime);
            ApiNetwork.hashRate(this.props.userId, {
                fromTimeStamp: fromTimeStamp,
                toTimeStamp: toTimeStamp
            }).then(response => {
                this.setState(state => {
                    return {
                        ...state,
                        hashRate: {
                            ...state.hashRate,
                            user: this.props.userId,
                            data: response.data,
                        }
                    }
                });
            });
        }
    };

    loadShareChart = (force = false) => {
        if (this.props.userId !== this.state.share.user || force) {
            this.setState(state => {
                return {
                    ...state,
                    share: {
                        ...state.share,
                        user: this.props.userId
                    }
                }
            });
            let fromTimeStamp = this.getTimeStamp(this.state.share.fromDate, this.state.share.fromTime);
            let toTimeStamp = this.getTimeStamp(this.state.share.toDate, this.state.share.toTime);
            ApiNetwork.share(this.props.userId, {
                fromTimeStamp: fromTimeStamp,
                toTimeStamp: toTimeStamp
            }).then(response => {
                this.setState(state => {
                    return {
                        ...state,
                        share: {
                            ...state.share,
                            user: this.props.userId,
                            data: response.data,
                        }
                    }
                });
            });
        }
    };

    loadDashboard = () => {
        if (this.props.userId !== this.state.userId) {
            this.props.getApiDashboard();
            this.setState({userId: this.props.userId});
        }
    }

    loadCharts = () => {
        this.loadHashRateChart();
        this.loadShareChart();
        this.loadDashboard();
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.loadCharts();
    }

    componentDidMount() {
        this.loadCharts();
    };

    render = () => {
        return (
            <div>
                <CardRow/>
                <Panel>
                    <ResponsiveContainer height={337}>
                        <LineChart data={this.state.hashRate.data}>
                            <XAxis dataKey="timestamp"
                                   angle={45}
                                   textAnchor="end"
                                   tickFormatter={timestamp => moment(new Date(1000 * timestamp)).format('HH:mm')}/>
                            <YAxis tickFormatter={value => (formatter.hashRate(value) + "H/s")}/>
                            <CartesianGrid strokeDasharray="3 3"/>
                            <Tooltip
                                isAnimationActive={false}
                                formatter={(value, name, props) => (formatter.hashRate(value) + "H/s")}
                                labelFormatter={(value) => moment(new Date(1000 * value)).format("YYYY-MM-DD HH:mm")}/>
                            <Line isAnimationActive={false} type="linear" dataKey="current" stroke="#42c3be"/>
                            <Line isAnimationActive={false} type="monotone" dataKey="avg" stroke="#5d62b5"/>
                            <Legend verticalAlign="top" height={36}/>
                        </LineChart>
                    </ResponsiveContainer>
                    <ChartFilter
                        onApply={() => this.loadHashRateChart(true)}
                        series={hashRateSeries}>
                        <label>From</label>
                        <DatePicker selected={this.state.hashRate.fromDate}
                                    onChange={(date) => this.setHashRateState("fromDate", date)}/>
                        <TimePicker value={this.state.hashRate.fromTime}
                                    onChange={(time) => this.setHashRateState("fromTime", time)}/>
                        <label>To</label>
                        <DatePicker selected={this.state.hashRate.toDate}
                                    onChange={(date) => this.setHashRateState("toDate", date)}/>
                        <TimePicker value={this.state.hashRate.toTime}
                                    onChange={(time) => this.setHashRateState("toTime", time)}/>
                    </ChartFilter>
                </Panel>
                <Panel>
                    <ResponsiveContainer height={337}>
                        <LineChart data={this.state.share.data}>
                            <XAxis dataKey="date"
                                   tickFormatter={timestamp => moment(new Date(1000 * timestamp)).format('HH:mm')}/>
                            <YAxis/>
                            <CartesianGrid strokeDasharray="3 3"/>
                            <Tooltip
                                isAnimationActive={false}
                                labelFormatter={(value) => moment(new Date(1000 * value)).format("YYYY-MM-DD HH:mm")}/>
                            <Line
                                isAnimationActive={false}
                                type="linear"
                                dataKey="valid"
                                stroke="#379f2c"/>
                            <Line
                                isAnimationActive={false}
                                type="monotone"
                                dataKey="invalid"
                                stroke="#d85e5a"/>
                        </LineChart>
                    </ResponsiveContainer>
                    <ChartFilter
                        onApply={() => this.loadHashRateChart(true)}
                        series={shareSeries}>
                        <label>From</label>
                        <DatePicker selected={this.state.share.fromDate}
                                    onChange={(date) => this.setShareState("fromDate", date)}/>
                        <TimePicker value={this.state.share.fromTime}
                                    onChange={(time) => this.setShareState("fromTime", time)}/>
                        <label>To</label>
                        <DatePicker selected={this.state.share.toDate}
                                    onChange={(date) => this.setShareState("toDate", date)}/>
                        <TimePicker value={this.state.share.toTime}
                                    onChange={(time) => this.setShareState("toTime", time)}/>
                    </ChartFilter>
                </Panel>
            </div>
        )
    };
}

const mapStateToProps = state => ({
    info: state.api.dashboard,
    userId: state.api.userId,
});

const mapDispatchToProps = dispatch => {
    return {
        getApiDashboard: () => dispatch(loadApiDashboardAsync(true))
    };
};

export const MainPageConnected = connect(mapStateToProps, mapDispatchToProps)(MainPage);

export default withLayout(UserLayout)(MainPageConnected);

