import React from 'react';


const modal = (props) => {
    return (
        <div>
            {props.display ? (
                <div className="modal">
                    <div className="modal-wrapper">
                        <div className="modal-back"/>
                        <div className="modal-content">
                            {props.children}
                            <div className="action-row">
                                <div className="btn-holder">
                                    <button className="btn gray" onClick={props.onClose}>Cancel</button>
                                </div>
                                <div className="btn-holder">
                                    <button className="btn blue" onClick={props.onSuccess}>{props.successTitle}</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            ) : null}
        </div>
    )
};

export default modal;
