import React from 'react';
import { connect } from "react-redux";
import Popup from "../../components/popup/Popup";
import Search from "../../layouts/main_layout/header/search/Search";
import MainLayout from "../../layouts/main_layout/MainLayout"
import Panel from '../../components/panel/Panel';
import { Link } from 'react-router-dom';
import * as formatter from '../../formatter';
import { ApiNetwork } from "../../network/api";
import { CompareObject } from "../../utils";
import Earning from "./earning/Earning";
import HashRate from "./hash_rate/HashRate";

const slider = (props) => {
    return (
        <div className="slider">
            <div className="left">
                <h1>{props.sliderHeader}</h1>
                <h2>{props.sliderSubTitle}</h2>
                <Link className="btn blue" to={props.sliderLink}>{props.sliderLinkTitle}</Link>
            </div>
            <div className="image"/>
            <div className="clear"/>
        </div>
    )
};

class IndexContent extends React.Component {
    state = {
        priceType: "usd",
        features: [],
        index: {
            sliderHeader: "The Mining Hub for Self-Sovereign Ergo Miners!",
            sliderSubTitle: "Ergopool is an impotent semi-decentralized mining pool built to empower the most decentralized blockchain ever existed.",
            sliderLink: "/how/",
            sliderLinkTitle: "Start mining",
            firstBoxTitle: "",
            firstBoxContent: "",
        }
    };

    changePriceType = () => {
        this.setState(state => {
            let priceType = "usd";
            if (state.priceType === "usd")
                priceType = "btc";
            return {...state, priceType: priceType};
        });
    };

    priceIcon = () => {
        if (this.state.priceType === "usd") return "$";
        return "btc";
    };

    render = () => {
        return (
            <div className="main-content">
                <Popup/>
                <h1>{this.props.index.firstBoxTitle}</h1>
                <h3>{this.props.index.firstBoxContent}</h3>
                <Search/>
                <div className="cols">
                    <div className="col">
                        <h1>Hash Rate </h1>
                        <h4>(last 30 minutes)</h4>
                        <h3>{formatter.hashRate(this.props.info.hash_rate.pool)}H/s</h3>
                    </div>
                    <div className="col">
                        <h1>Active Miners</h1>
                        <h3>{this.props.info.active_miners}</h3>
                    </div>
                    <div className="col">
                        <h1>Miners</h1>
                        <h3>{this.props.info.miners}</h3>
                    </div>
                    <div className="col">
                        <h1>Blocks/h</h1>
                        <h3>{this.props.info.blocks_in_hour}</h3>
                    </div>
                    <div className="col">
                        <h1>Price <i className="ep-icon-exchange" onClick={this.changePriceType}/></h1>
                        <h3>{this.props.info.price[this.state.priceType]} {this.priceIcon()}</h3>
                    </div>
                </div>
                <div className="circle">
                    <i className="ep-icon-star"/>
                </div>
                <h1>Features</h1>
                <div className="features">
                    {this.props.features.map((item, index) => (
                        <div className="panel-container" key={index}>
                            <div className="panel-wrapper">
                                <Panel>
                                    {item.title}
                                </Panel>
                            </div>
                        </div>
                    ))}
                </div>
                <h1 id="calculator">Calculate your Earning</h1>
                <Panel className={"panel-earning"}>
                    <HashRate networkHashRate={this.props.info.hash_rate.network}
                              poolHashRate={this.props.info.hash_rate.pool}/>
                    <Earning {...this.props.info.hash_rate}/>
                </Panel>
            </div>
        )
    };
}

class Index extends React.Component {
    state = {
        features: [],
        index: {
            sliderHeader: "",
            sliderSubTitle: "",
            sliderLink: "",
            sliderLinkTitle: "",
            firstBoxTitle: "",
            firstBoxContent: "",
        }
    };
    getData = () => {
        ApiNetwork.getUiJson("Index.json").then(index => {
            if (CompareObject(index.items, this.state.features) === false || CompareObject(index.extra, this.state.index) === false) {
                this.setState({
                    features: index.items,
                    index: index.extra
                });
            }
        });
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.getData();
    }

    componentWillMount() {
        this.getData();
    }

    render = () => {
        return (
            <MainLayout hide_search={true} customClass="index" title={slider(this.state.index)} {...this.props}>
                <IndexContent features={this.state.features} index={this.state.index} {...this.props}/>
            </MainLayout>
        )
    }
}

const mapStateToProps = state => ({
    info: state.api.info,
});

export default connect(mapStateToProps)(Index);
